body::-webkit-scrollbar {
    width: 0;
}

.main_body_section {
    overflow: hidden;
}

.video_page {
    position: relative;
    min-height: 100vh;
    overflow: hidden;

    .content {
        position: absolute;
        width: 100%;
        height: 100vh;
    }

    .video_block {
        position: relative;
        height: 100vh;

        video {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    .top_video_btn {
        position: absolute;
        right: 20px;
        top: 13px;

        @include media ("<tablet") {
            top: auto;
            bottom: 40px;
            right: 16px;
        }

        .btn_black {
            font-family: IBM Plex Mono;
            padding: 28.5px 32px;
            cursor: pointer;
            position: relative;
            font-size: 20px;
            font-family: IBM Plex Mono;

            @include media ("<tablet") {
                padding: 15.5px 32px;

            }
        }

        .hide {
            display: none;
        }
    }

    .bottom_video_btn {
        position: absolute;
        right: 20px;
        bottom: 32px;

        @include media ("<tablet") {
            right: 16px;
            left: 16px;
            bottom: 36px;
            transform: none;
            right: 86px;

        }
    }

    .btn_black {
        font-family: IBM Plex Mono;
        display: inline-block;
        position: relative;
        width: 100%;
        color: $white;
        font-size: 20px;
        font-weight: 500;
        line-height: 25.6px;
        letter-spacing: -0.14px;
        text-transform: uppercase;
        padding: 28.5px 58px;
        border: 1px solid $white;
        transition: $transition_all_03;
        background: linear-gradient(0deg, rgba(255, 255, 255, 0.05), rgba(255, 255, 255, 0.05));
        backdrop-filter: blur(10px);

        @include media ("<tablet") {
            padding: 18px 0;
            text-align: center;
        }


        .left_arrow,
        .right_arrow {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
        }

        .left_arrow {
            left: 32px;

        }

        .right_arrow {
            right: 32px;

        }

        svg {
            fill: $white;
            transition: $transition_all_03;
        }

        &:hover {
            background-color: $white;
            transition: $transition_all_03;
            color: $black;

            svg {
                fill: $black;
                transition: $transition_all_03;
            }
        }
    }

    .turn_sound {
        border: 1px solid #FFFFFF;
        padding: 24px;
        position: absolute;
        left: 20px;
        bottom: 32px;
        border-radius: 50%;
        background: linear-gradient(0deg, rgba(255, 255, 255, 0.05), rgba(255, 255, 255, 0.05));

        @include media ("<tablet") {
            bottom: 36px;
            left: auto;
            right: 16px;
            padding: 16px;
        }

    }
 
    .text_main_video {
        font-family: Neue Haas Grotesk Display Pro;
        font-size: 48px;
        font-weight: 500;
        line-height: 48px;
        color: $white;
        position: absolute;
        top: 160px;
        text-transform: uppercase;
        letter-spacing: -3px;
    }

    .text_video_1 {
        left: 20px;
        max-width: 430px;
        --animate-duration: 4s; 
    }

    .text_video_2 {
        right: 20px;
        max-width: 480px;
        --animate-duration: 4s;
    }
}

.brain_section {
    font-family: Neue Haas Grotesk Display Pro;
    height: 100vh;
    width: 100%;
    display: flex;
    align-items: center;
    position: relative;

    @include media ("<tablet") {
        align-items: start;
        padding-top: 80px;
    }

    .img_brain_block {
        position: absolute;
        right: 0;
        z-index: 1;

        @include media ("<tablet") {
            width: 200%;
            top: 200px;
        }
    }

    .container {
        display: flex;
        align-items: center;
        position: relative;

        @include media ("<tablet") {
            display: block;
        }
    }

    .content {
        max-width: 865px;
        width: 100%;
        margin: 0;
        z-index: 2;
    }

    .brain_title {
        color: $white;
        @include adaptiv-vaule("font-size", 132, 52, 1);
        font-style: normal;
        font-weight: 500;
        letter-spacing: -3px;
        text-transform: uppercase;
        @include adaptiv-vaule(" margin-bottom", 24, 16, 1);
    }


    .brain_text {
        color: $white;
        @include adaptiv-vaule("font-size", 20, 16, 1);
        font-style: normal;
        font-weight: 500;
        max-width: 397px;
        letter-spacing: -1px;
        @include adaptiv-vaule(" margin-bottom", 163, 44, 1);
    }

    .brain_text_first {
        margin-bottom: 10px;
    }

}


.button_form_section {
    height: 100vh;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    background: url(../images/main_page/background-form.png);

    .container {
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .content {
        max-width: 865px;
        width: 100%;
        margin: 0 auto;
    }

    .button_form_title {
        color: $white;
        text-align: center;
        @include adaptiv-vaule("font-size", 132, 52, 1);
        font-style: normal;
        font-weight: 500;
        letter-spacing: -3px;
        text-transform: uppercase;

        @include media ("<tablet") {
            margin-bottom: 44px;
        }
    }

    .btn_white_circle {
        cursor: pointer;
        width: 172px;
        height: 172px;
        color: $black;
        font-size: 14px;
        font-weight: 500;
        line-height: 17.64px;
        letter-spacing: -1px;
        text-transform: uppercase;
        border: 1px solid $white;
        transition: $transition_all_03;
        position: absolute;
        bottom: 100px;
        right: 100px;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        background: $white;

        @include media ("<desktop") {
            position: relative;
            margin-left: auto;
            margin-right: auto;
            bottom: 0;
            right: 0;
        }

        .left_arrow,
        .right_arrow {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
        }

        .left_arrow {
            left: 16px;
        }

        .right_arrow {
            right: 16px;
        }

        svg {
            fill: $black;
            transition: $transition_all_03;
        }

        &:hover {
            background-color: transparent;
            transition: $transition_all_03;
            color: $white;

            svg {
                fill: $white;
                transition: $transition_all_03;
            }
        }
    }

}


//pop_up
.body_lock {
    overflow: hidden !important;
}

.overlay-popup {
    background: #0E0E0ECC;
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    z-index: 10;
    display: none;

    &.active {
        display: block;
    }
}

.popup {
    display: none;
    position: fixed;
    top: 0;
    left: 50%;
    transform: translate(-50%);
    width: 100%;
    z-index: 10;

    &.active {
        display: block;
    }
}

.exspand_btn {
    position: absolute;
    top: 32px;
    right: 32px;
    z-index: 15;

    button {
        display: inline-block;
        width: 100%;
        color: #fff;
        font-size: 14px;
        font-weight: 500;
        line-height: 17.6px;
        letter-spacing: -0.14px;
        text-transform: uppercase;
        padding: 15px 16px 15px 14px;
        border: none;
        transition: all 0.3s ease;
        background: none;
        display: flex;
        align-items: center;
        &::before {
            content: '';
            background: url(../images/main_page/expand-icon.svg);
            display: block;
            width: 24px;
            height: 24px;
            margin-right: 10px;
        }
        &::after {
            content: 'Expand';
        }
        .icon_btn {
            display: flex;
            align-items: center;

            svg {
                margin-right: 10px;
            }
        }
    }
}

.close_popup {
    position: absolute;
    width: 32px;
    height: 32px;
    z-index: 5;
    top: 40px;
    left: 32px;
    cursor: pointer;

    span {
        position: absolute;
        top: 11px;
        left: 0;
        width: 100%;
        height: 2px;
        background-color: $white;
        border-radius: 10px;
        transition: all .7s ease 0s;
        opacity: 0;

    }

    &::before,
    &::after {
        content: '';
        position: absolute;
        right: 0;
        height: 2px;
        width: 100%;
        background-color: $white;
        border-radius: 10px;
    }

    &::before {
        top: 15px;
        transform: rotate(45deg);
    }

    &::after {
        bottom: 15px;
        transform: rotate(-45deg);
    }


}

.contact_form_section {
    height: 100vh;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;

    .container {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 100%;
    }

    .back_form {
        max-width: 790px;
        width: 100%;
        background: rgba(255, 255, 255, 0.05);
        backdrop-filter: blur(5px);
        transition: all 1s ease;
    }

    .content {
        margin: 0 auto;
        max-width: 790px;
        padding: 68px;
        position: relative;
        transition: all 1s ease;
    }

    .contact_title {
        color: $white;
        text-align: center;
        @include adaptiv-vaule("font-size", 70, 42, 1);
        font-style: normal;
        font-weight: 500;
        letter-spacing: -2.1px;
        text-transform: uppercase;
        @include adaptiv-vaule(" margin-bottom", 35, 45, 1);
    }

    form,
    .inpur_wrap {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
    }

    .inpur_wrap {
        margin-bottom: 15px;
    }

    .area {
        margin-bottom: 25px;
    }

    .input_label {
        color: $white;
        font-size: 12px;
        font-weight: 600;
        line-height: 126%;
        letter-spacing: -0.12px;
        text-transform: uppercase;
        margin-bottom: 5px;
    }

    .data_text,
    .area_text {
        border: 1px solid #FFF;
        background: #262626;
        width: 100%;
        padding: 18px 16px;
        color: $white;
        font-size: 14px;
        transition: $transition_all_03;

        &:focus {
            border: 1px solid $gray_A6;
            transition: $transition_all_03;
        }

        &::placeholder {
            color: $gray_A6;
            font-size: 14px;
            font-weight: 500;
            line-height: 126%;
            letter-spacing: -0.14px;
            text-transform: uppercase;
        }
    }

    .area_text {
        height: 165px;
    }

    .form_btn {
        width: 100%;
        color: $black;
        font-size: 14px;
        font-weight: 500;
        line-height: 126%;
        letter-spacing: -0.14px;
        text-transform: uppercase;
        padding: 18px 16px;
        border: 1px solid $white;
        transition: $transition_all_03;
        position: relative;

        .left_arrow,
        .right_arrow {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
        }

        .left_arrow {
            left: 16px;

        }

        .right_arrow {
            right: 16px;
        }

        svg {
            fill: $black;
            transition: $transition_all_03;
        }

        &:hover {
            background-color: transparent;
            transition: $transition_all_03;
            color: $white;

            svg {
                fill: $white;
                transition: $transition_all_03;
            }
        }
    }

    .site_btn_white:hover .left_arrow {
        left: 35%;
    }

    .site_btn_white:hover .right_arrow {
        right: 35%;
    }
}

.safari_button {
