.swiper_beach_club_section {
    .swiper_beach_club_title {
        max-width: 710px;
        width: 100%;
        margin: 0 auto;
        text-align: center;
        margin-bottom: 30px;
        font-size: 42px;
        @include adaptiv-vaule("font-size", 42, 24, 1);
        font-weight: 600;
        line-height: 124%;
        letter-spacing: -0.42px;
    }

    .swiper {
        position: relative;

        &::before {
            content: '';
            position: absolute;
            height: 100%;
            width: 15%;
            z-index: 2;
            top: 0;
            left: 0;
            background: linear-gradient(270deg, rgba(14, 14, 14, 0.00) 0%, #0E0E0E 100%);

            @include media ("<tablet") {
                width: 5%;
            }
        }

        &::after {
            content: '';
            position: absolute;
            top: 0;
            right: 0;
            height: 100%;
            width: 15%;
            z-index: 2;
            background: linear-gradient(90deg, rgba(14, 14, 14, 0.00) 0%, #0E0E0E 100%);

            @include media ("<tablet") {
                width: 5%;
            }
        }

    }

    .swiper_beach_club {
        padding-top: 100px;
        padding-bottom: 100px;

        @include media ("<desktop") {
            padding-bottom: 0;
            padding-top: 50px;
        }

        .swiper-slide {
            transform: translateY(50px);
            transition: $transition_all_03;
        }

        .swiper-slide-active {
            transform: translateY(0);
        }
    }

    .swiper-horizontal>.swiper-pagination-bullets,
    .swiper-pagination-bullets.swiper-pagination-horizontal,
    .swiper-pagination-custom,
    .swiper-pagination-fraction {
        top: 0;
        height: 2px;
    }

    .swiper-pagination-bullet {
        width: 60px;
        height: 2px;
        background-color: rgba(255, 255, 255, 0.3);
        opacity: 1;
        border-radius:0;
    }

    .swiper-pagination-bullet-active {
        background-color: $white;
    }

    .slide_img {
        @include adaptiv-vaule("height", 650, 250, 1);
        border-radius: 20px;
        overflow: hidden;
        margin: 0 auto;

        @include media ("<tablet") {
            padding: 0 15px;
        }

        img {
            width: 100%;
            height: 100%;
        }
    }

    .arrows {
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
        display: flex;
        align-items: center;
        width: 150px;
        height: 40px;

        @include media ("<desktop") {
            display: none;
        }

        .swiper-button-next {
            right: 0;
            background-image: url(images/across_us_template/AR_Right.svg);
            width: 40px;
            height: 37px;
            background-repeat: no-repeat;
            background-position: center;
            background-size: 100% 100%;

            &::after {
                display: none;
            }
        }

        .swiper-button-prev {
            left: 0;
            background-image: url(images/across_us_template/AR_Left.svg);
            width: 40px;
            height: 35px;
            background-repeat: no-repeat;
            background-position: center;
            background-size: 100% 100%;

            &::after {
                display: none;
            }
        }
    }
}

.mobile_beach_club {
    .about_img {
        &::before {
            display: block;
        }
    }
}

.shop_services {
    @include adaptiv-vaule("padding-top", 80, 40, 1);
    @include adaptiv-vaule("padding-bottom", 100, 50, 1);
    border-bottom: 1px solid rgba(255, 255, 255, 0.15);

    .shop_services_title {
        font-size: 108px;
        @include adaptiv-vaule("font-size", 108, 40, 1);
        font-weight: 500;
        line-height: 105%;
        letter-spacing: -3.24px;
        text-transform: uppercase;
        text-align: center;
        max-width: 1120px;
        margin: 0 auto;
        animation: fadeIn;
        animation-duration: 4s;
        margin-bottom: 15px;

        @include media ("<tablet") {
            margin-bottom: 50px;
        }

        &::after,
        &::before {
            @include adaptiv-vaule("height", 50, 25, 1);
        }
    }

    .shop_img_wrap {
        position: relative;

        @include media ("<tablet") {
            text-align: center;

            &::after,
            &::before {
                display: none;
            }
        }
    }

    .shop_img {
        @include adaptiv-vaule("height", 1100, 500, 1);
    
        @include media ("<tablet") {
            height: 540px;
            width: 100%;
            margin: 0 auto;
            margin-bottom: 60px;
            position: relative;
        
            &::before,
            &::after {
                content: '';
                position: absolute;
                z-index: 1;
                left: 0;
                @include adaptiv-vaule("height", 280, 40, 1);
                width: 100%;
                background: linear-gradient(0deg, rgba(14, 14, 14, 0.00) 0%, #0E0E0E 100%);
            }
        
            &::before {
                top: 0;
            }
        
            &::after {
                bottom: 0;
                background: linear-gradient(180deg, rgba(14, 14, 14, 0.00) 0%, #0E0E0E 100%);
            }
        }

        @include media ("<phone") {
            height: 300px;
            width: 100%;
        }

        img{
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;
            object-fit: cover;
        }
    }

    .item_wrap {
        display: flex;
        align-items: center;
        justify-content: space-between;
        max-width: 1100px;
        padding: 0 15px;
        width: 100%;
        margin: 0 auto;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);

        @include media ("<tablet") {
            justify-content: space-around;
            position: static;
            padding: 40px 15px;
            transform: translate(0, 0);
            gap: 20px;
        }

    }

    .item_list {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        @include adaptiv-vaule("gap", 120, 40, 1);

        @include media ("<phone_max") {
            align-items: center;
            gap: 30px;
        }
    }

    .item {
        display: flex;
        flex-direction: column;
    }

    .left {
        align-items: flex-start;

        @include media ("<tablet") {
            align-items: center;
        }
    }

    .right {
        align-items: flex-end;

        @include media ("<tablet") {
            align-items: center;
        }
    }

    .icon {
        width: 84px;
        height: 84px;
        @include adaptiv-vaule("width", 83, 50, 1);
        @include adaptiv-vaule("height", 83, 50, 1);
        @include adaptiv-vaule("margin-bottom", 25, 15, 1);
    }

    .item_title {
        color: $white;
        @include adaptiv-vaule("font-size", 24, 16, 1);
        font-weight: 600;
        line-height: 112%;
        text-transform: uppercase;
        margin-bottom: 15px;
    }

    .sub-title {
        color: rgba(255, 255, 255, 0.60);
        text-align: center;
        @include adaptiv-vaule("font-size", 14, 12, 1);
        font-weight: 400;
        line-height: 112%;
        letter-spacing: 0.56px;
        text-transform: uppercase;
    }
}

.beach_club_video{
    border: none;
}

.typography_beach_club {
    .gradient_block {
        background: linear-gradient(90deg, #211F19 0%, #DDD4C5 49.48%, #C99E59 98.96%);

        @include media ("<phone_max") {
            background: linear-gradient(180deg, #211F19 0%, #DDD4C5 49.48%, #C99E59 98.96%);
        }
    }

    .color_name {
        &:last-child {
            color: $black;
        }
    }
}